body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 45px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 2em;
  margin-top: 0;
}

.App-intro {
  font-size: large;
}

.navio {
  z-index: 10 !important;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 403px;
  height: 340px!important;
  overflow-x: scroll;
  background-color: #edf0f4;
}

.navio > #navio {
  margin-top: 20px;
}

#voyager-embed {
  height: 100vh;
}

.navio canvas {
  position: absolute;
  left: 0 !important;

}

.Pane.vertical.Pane1 {
  width: 200px!important;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media only screen and (max-height: 870px) {
  #voyager-embed {
    height: 120vh;
  }
  .navio {
    bottom: -20vh;
  }
}

@media only screen and (max-height: 720px) {
  #voyager-embed {
    height: 140vh;
  }
  .navio {
    bottom: -40vh;
  }
}
.information {
  width: 80%;
  margin: 0 10%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.information__title {
  font-weight: 500;
  font-family: 'Roboto', sans-serif;
  font-size: 25px;
  margin: 5px;
}

.information__text {
  font-size: 17px;
  color: rgba(0, 0, 0, .60);
  font-family: 'Open Sans', sans-serif;
  text-align: justify;
  line-height: 1.8;
  margin: 5px;
}
.chip {
  margin: 10px;
}

.chip__circle {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  cursor: pointer;
}
.home {
  width: 100vw;
  height: 100vh;
  display: flex;
}

.home__information-column {
  width: 70vw;
  height: 100vh;
  border-right: solid rgba(0, 0, 0, .3) 1px;
  overflow: hidden;
}

/*.home__dialog:nth-child(1) {*/
  /*max-width: 1000px !important;*/
/*}*/

.home__options-column {
  width: 30vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.home__title {
  font-size: 36px;
  color: rgba(0, 0, 0, .87);
  font-weight: 500;
  font-family: 'Roboto', sans-serif;
  margin: 5px 0 20px;
}

.home__main-title {
  font-size: 96px;
  font-family: 'Roboto', sans-serif;
  font-weight: 200;
  text-transform: uppercase;
  color: rgba(0, 0, 0, .87);
  margin: 5px;
}

.home__button {

  /*box-shadow: 0 19px 38px rgba(0,0,0,0.30), 0 15px 12px rgba(0,0,0,0.22);*/
  cursor: pointer;
  color: white !important;
  background-color: #2196F3 !important;
}

.home__chips-container {
  width: auto;
  position: absolute;
  bottom: 20px;
  left: 2vw;
  display: flex;
}

.home__learn-more-option {
  position: absolute;
  right: 2vw;
  bottom: 20px;
}

.home__learn-more-button {
  margin: 10px !important;
  border-style: hidden !important;
  cursor: pointer;
}

.home__nautilus-gif {
  max-width: 25vw;
  margin-bottom: 5vh;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.dialog-button {
  color: #2196F3 !important;
}

.home__card {
  min-width: 275px;
  margin: 5px;
}

.home__cards-container {
  width: 100%;
  display: flex;
}

.home__card-text {
  text-align: justify;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.7);
}

.home__card-title {
  margin: 10px 0;
  font-size: 18px;
  color: rgba(0, 0, 0, 0.9);
  text-align: center;
}

.home__card-content {
  margin: 10px 0 !important;
}

.home__dialog-input button {
  background-color: #2196F3;
  color: white;
}

.c27 {
  max-width: 1000px!important;
}

input[type=file] {
  cursor: pointer;
  width: 180px;
  height: 34px;
  overflow: hidden;
}

input[type=file]:before {
  width: 158px;
  height: 32px;
  font-size: 16px;
  line-height: 32px;
  content: 'Select your file';
  display: inline-block;
  background: white;
  border: 1px solid rgba(0, 0, 0, 0.5);
  color: #222222;
  padding: 0 10px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

input[type=file]::-webkit-file-upload-button {
  visibility: hidden;
}

